<script>
import { dataManage as api } from '../../../api'
export default {
  name: 'inspectionOfficeDetail',
  data() {
    return {
      tableData: [],
      baseUrl: '',
      relativeId: '',
      name: '',
      insDetailTitleObj: {
        CheckPending: '', // 
        report: '', // 
        ureport: '' // 
      }
    }
  },
  mounted() {
    this.name = this.$route.query.name
    this.baseUrl = window.api_host
    this.query()
  },
  methods: {
    query() {
      const params = {
        name: this.name
      }
      api
        .getSelectAllDetail(params)
        .then(rtn => {
          console.log(rtn)
          this.tableData = rtn.data.data
          this.insDetailTitleObj.CheckPending = rtn.data.CheckPending.join()
          this.insDetailTitleObj.report = rtn.data.report.join()
          this.insDetailTitleObj.ureport = rtn.data.ureport.join()
        })
        .catch(err => {
          this.pageState = err
        })
    },
    exportExcel() {
      location.href =
        this.baseUrl + '/dataUploadData/alldataExcel?name=' + this.name
    },
    goBack() {
      history.go(-1)
    }
  },
  components: {}
}
</script>
